.contacts {
  border-top: 1px solid #74819c;
  padding: 78px 0 64px;
  flex-basis: 50%;

  h2 {
    font-family: $ff-secondary;
    font-size: 54px;
    font-weight: 600;
    letter-spacing: 1.62px;
    margin: 0 0 56px;
  }

  @include vp-1199 {
    padding: 75px 160px 65px 132px;

    h2 {
      font-size: 42px;
      letter-spacing: 1.26px;
    }
  }

  @include vp-767 {
    padding: 70px 15px 35px;

    h2 {
      font-size: 32px;
      letter-spacing: 0.96px;
      margin: 0 0 50px;
    }
  }
}

.contacts__wrapper {
  max-width: 440px;
  margin: 0 auto;

  @include vp-1199 {
    max-width: 476px;
  }
}

.contacts__inner-wrapper {
  display: flex;

  @include vp-767 {
    display: block;
  }
}

.contacts__column {
  padding: 0 65px 0 0;
  border-right: 1px solid #74819c;

  &:last-child {
    padding: 0 0 0 58px;
    margin-right: 0;
    border: none;
  }

  @include vp-1199 {
    padding: 0 50px 0 0;

    &:last-child {
      padding: 0 0 0 46px;
    }
  }

  @include vp-767 {
    padding: 0;
    border: none;
    margin-bottom: 30px;

    &:last-child {
      padding: 0;
    }
  }
}

.contacts__contact {
  margin-bottom: 17px;

  &:last-child {
    margin-bottom: 0;
  }

  h3 {
    font-size: 16px;
    line-height: 24px;
    margin: 0 0 5px;
  }

  p {
    font-size: 14px;
    line-height: 21px;
    margin: 0;
  }

  a {
    color: inherit;

    &:hover {
      opacity: 0.8;
    }

    &:focus-visible {
      outline: 1px solid $color-sapphire;
    }

    &:active {
      opacity: 0.3;
    }

    &--disabled {
      opacity: 0.3;
      pointer-events: none;
    }
  }

  @include vp-1199 {
    margin-bottom: 30px;

    h3 {
      font-size: 20px;
      line-height: 10px;
      margin: 0 0 20px;
    }

    p {
      font-size: 18px;
    }
  }

  @include vp-767 {
    margin-bottom: 35px;

    h3 {
      margin: 0 0 18px;
    }
  }
}
