  .free h2 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin: 0 auto 38px;
  }
}

.free__form {
  max-width: 410px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .free__form {
    max-width: 472px;
    margin: 0 auto;
  }
}

.free__input {
  width: 190px;
  margin-right: 30px;
}
.free__input:last-child {
  margin-right: 0;
}
.free__input p {
  display: none;
  margin: 5px 0 0;
  font-size: 14px;
  line-height: 14px;
  color: #ff121f;
}
.free__input input {
  color: #ffffff;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #74819c;
  font-size: 14px;
  padding: 5px 0;
  width: 100%;
}
.free__input input::placeholder {
  color: #ffffff;
}
.free__input input:hover {
  border-bottom: 1px solid #ffffff;
}
.free__input input:focus-visible {
  padding: 5px;
  outline: 1px solid #ffffff;
}
.free__input input:focus-visible::placeholder {
  color: transparent;
}
.free__input input:disabled {
  opacity: 0.3;
}
@media (max-width: 1199px) {
  .free__input {
    width: 207px;
    margin-right: 60px;
  }
}
@media (max-width: 767px) {
  .free__input {
    width: 100%;
    margin: 0 0 33px;
  }
  .free__input:last-child {
    margin: 0;
  }
}

.free__input.is-invalid input {
  border-bottom: 1px solid #ff121f;
}
.free__input.is-invalid p {
  display: block;
}

.free__input-wrapper {
  display: flex;
  margin-bottom: 30px;
}
@media (max-width: 1199px) {
  .free__input-wrapper {
    margin-bottom: 28px;
  }
}
@media (max-width: 767px) {
  .free__input-wrapper {
    display: block;
    margin-bottom: 40px;
  }
}

.free__button {
  background-color: transparent;
  border: 3px solid #ffffff;
  width: 100%;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  padding: 15px;
  color: #ffffff;
  cursor: pointer;
  user-select: none;
}
.free__button:hover, .free__button:focus-visible {
  background-color: #ffffff;
  color: #103aae;
  outline: none;
}
.free__button:active {
  color: #102654;
}
.free__button--disabled {
  opacity: 0.5;
  pointer-events: none;
}

